<script>
  import { computed, ref } from "@vue/reactivity";
  import { watch } from "@vue/runtime-core";
  export default {
    props: {
      searchResult: {
        type: Array,
        default: () => [],
      },
      isSearch: {
        type: Boolean,
        default: false,
      },
      lv2Type: {
        type: String,
        default: "",
      },
      loading: {
        type: Object,
        default: () => ({
          imgOk: false,
        }),
      },
    },
    setup(props) {
      let notFoundImg = ref(null);
      import("@/assets/NoImage-img/NoImage-255x200.png").then((res) => {
        notFoundImg.value = res.default;
      });
      const showResult = computed(
        () =>
          props.isSearch && props.searchResult.length && props.loading.imgOk,
      );
      const apiID = computed(() => {
        switch (props.lv2Type) {
          case "activity":
            return "ActivityID";
          case "scientSpot":
            return "ScenicSpotID";
          case "restaurant":
            return "RestaurantID";
        }
      });
      const apiResult = computed(() => {
        console.log(props.searchResult);
        return props.searchResult.map((item) => {
          console.log("item.name", item.ActivityName);
          let name = item.ActivityName
            ? item.ActivityName
            : item.ScenicSpotName
            ? item.ScenicSpotName
            : item.RestaurantName;
          let imgUrl = item.Picture.PictureUrl1
            ? item.Picture.PictureUrl1
            : notFoundImg.value;
          let location = item.City
            ? item.City
            : item.Location
            ? item.Location
            : item.Address;
          let routerLink = `/${props.lv2Type}/${item[apiID.value]}`;
          console.log("apiID", apiID.value);
          return {
            name,
            imgUrl,
            location,
            routerLink,
          };
        });
      });
      //計算 loading 時間
      watch(
        () => apiResult.value,
        (newVal) => {
          console.log(newVal);
          const total = newVal.length;
          let num = 0;
          let timeUp = false;
          setTimeout(() => {
            props.loading.imgOk = true;
            console.log("提早跳出");
            timeUp = true;
          }, 5000);
          newVal.forEach((item) => {
            const image = new Image();
            image.src = item.imgUrl;
            image.onload = () => {
              num++;
              console.log("img success");
              if (num === total && !timeUp) {
                setTimeout(() => {
                  props.loading.imgOk = true;
                }, 800);
              }
            };
            image.onerror = () => {
              num++;
              console.log("img error");

              if (num === total) {
                props.loading.imgOk = true;
              }
            };
          });
        },
      );
      const HandErrorImg = (img) => {
        img.target.src = notFoundImg.value;
      };
      return { showResult, apiResult, notFoundImg, HandErrorImg };
    },
  };
</script>
<template>
  <div v-if="showResult" class="result-container">
    <div class="cards">
      <div v-for="item in apiResult" :key="item.name" class="card">
        <router-link class="card-imgWrap" :to="item.routerLink">
          <img :src="item.imgUrl" @error="HandErrorImg($event)" />
        </router-link>
        <div class="card-text">
          <h3>{{ item.name }}</h3>
          <div class="card-location">
